import responsiveWindowMixin from 'kolibri.coreVue.mixins.responsiveWindowMixin';
import commonCoreStrings from 'kolibri.coreVue.mixins.commonCoreStrings';
import commonTaskStrings from 'kolibri.coreVue.mixins.commonTaskStrings';
import { TaskStatuses } from '../../constants';

export default {
  name: 'FacilityTaskPanelDetails',
  mixins: [commonCoreStrings, responsiveWindowMixin, commonTaskStrings],
  props: {
    task: {
      type: Object,
      required: true,
    },
    statusMsg: {
      type: String,
      required: true,
    },
    headingMsg: {
      type: String,
      required: true,
    },
    underHeadingMsg: {
      type: String,
      required: false,
    },
    underProgressMsg: {
      type: String,
      required: false,
    },
    showCircularLoader: {
      type: Boolean,
      default: false,
    },
    loaderType: {
      type: String,
      required: false,
      validator(value) {
        return value === 'determinate' || value === 'indeterminate';
      },
    },
    buttonSet: {
      type: String,
      required: false,
      validator(value) {
        return value === '' || value === 'clear' || value === 'cancel' || value === 'retry';
      },
    },
  },
  computed: {
    taskIsCompleted() {
      return this.task.status === TaskStatuses.COMPLETED;
    },
    taskIsCanceling() {
      return this.task.status === TaskStatuses.CANCELING;
    },
    taskIsCanceled() {
      return this.task.status === TaskStatuses.CANCELED;
    },
    taskIsFailed() {
      return this.task.status === TaskStatuses.FAILED || this.taskIsCanceled;
    },
    taskPercentage() {
      return this.task.percentage;
    },
    startedByMsg() {
      if (!this.task.started_by_username) {
        return '';
      }
      return this.getTaskString('taskStartedByLabel', {
        username: this.task.started_by_username || this.getTaskString('unknownUsername'),
      });
    },
    statusHidesLoader() {
      return (
        this.task.status === TaskStatuses.PENDING ||
        this.taskIsCompleted ||
        this.taskIsCanceling ||
        this.taskIsFailed
      );
    },
  },
};

